:root
{
  --yellow: #c2f155;
    --orange : #99ec54;
    --black : #207242;
    --gray : #788097;
    --blueCard : #ddfee7;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(126, 247, 126, 0.568);
    --smboxShadow: -79px 51px 60px rgba(72, 133, 77, 0.08);
}



.App{
    padding: 0.5rem 2.5rem;
    overflow: hidden;
    color: var(--black);   
    background-color: #eefff2; 
}

.button {
    background: linear-gradient(180deg, #71f3ac 26.71%, #22eb4d 99.36%);
    box-shadow: 0px 20px 24px 3px rgba(112, 231, 32, 0.42);
    border-radius: 34px;
    border: none;
    color: white;
    font-size: 12px;
    padding: 11px 26px 11px 26px;
  }
  
  .button:hover {
    background: rgb(132, 202, 75);
    cursor: pointer;
    border: 1px solid var(--orange);
    color: var(--orange);
  }


  span{
    font-size: 16px;

  }

  
  @media screen and (max-width: 480px) {
    .App {
      padding: 0.5rem 1rem;

    }
  }