.works {
  display: flex;
  height: 60vh;
  margin-top: 3rem;
  padding: 30px;
  padding-top: 120px;
}
/* left side */

.w-left {
  display: flex;
  position: relative;
  flex: 1;
  flex-direction: column;
}

.s-button {
  width: 8rem;
  height: 2rem;
  margin-top: 4rem;
}

/* right side */
.w-right {
  flex: 1;
  position: relative;
  padding: 0;
  transform: scale(0.85);
}

.w-mainCircle {
  left: 9rem;
  position: relative;
  width: 18rem;
  height: 18rem;
  border-radius: 100%;
  box-shadow: var(--smboxShadow);
  top: 2rem;
  background: white;
  /* darkMode */
  z-index: 2;
}
.w-mainCircle > * {
  position: absolute;
}
.w-mainCircle > :nth-child(1) {
  top: -3rem;
  left: 6rem;
}
.w-mainCircle > :nth-child(2) {
  left: -3rem;
  top: 5rem;
}
.w-mainCircle > :nth-child(3) {
  left: 6rem;
  top: 5rem;
}
.w-mainCircle > :nth-child(4) {
  left: 15rem;
  top: 5rem;
}
.w-mainCircle > :nth-child(5) {
  left: 6rem;
  top: 13rem;
}
.w-secCircle {
  width: 6rem;
  height: 6rem;
  position: absolute;
  border-radius: 100%;
  border: 5px solid #ebebeb;
  display: flex;
  align-items: center;
  justify-content: center;
  background: "none";
  box-shadow: var(--smboxShadow);
  background: white;
}

.w-secCircle > img {
  transform: scale(0.6);
}

.w-backCircle {
  position: absolute;
  height: 13rem;
  width: 13rem;
  border-radius: 100%;
  /* darkMode */
  z-index: 1;
}
.blueCircle {
  background: #95ff80;
  left: 18rem;
  top: 0rem;
}
.yellowCircle {
  background: #20b60c;
  left: 18rem;
  top: 8rem;
}

.fresh {
  font-weight: bold;
  font-size: 25px;
}




@media screen and (max-width: 1200px) {
  .works {
    display: block;
    height: auto;
padding-top: 0%;
  }

 
  .w-right {
    width: 100%; 
    padding: 0;
  }

  .w-right {
    display: none;
    

  }

  

}
